:root {
  color: #363636;
  font-family: Merriweather, serif;
  overflow-x: hidden;
}

h1 {
  font-size: 36px;
}

h2 {
  margin: 0;
  padding: 0;
}

hr {
  width: 100%;
  height: 1px;
  background-color: #363636dd;
  border: none;
}

.container {
  max-width: 65vw;
  margin: auto;
}

.margin-padding-0, .margin-padding-0 p {
  margin: 0;
  padding: 0;
}

.audio-controls {
  flex-flow: row;
  display: flex;
}

.audio-control-buttons {
  width: 64px;
  height: 64px;
  position: relative;
}

.audio-control-button {
  width: 56px;
  height: 56px;
  cursor: pointer;
  opacity: .8;
  transition: opacity .1s;
  position: absolute;
  top: 0;
  left: 0;
}

.audio-control-button:hover {
  opacity: 1;
}

.audio-slider-container {
  width: 100%;
}

.audio-controls-right-side {
  width: 100%;
  flex-flow: column;
  margin: auto 4px auto 20px;
  display: flex;
}

.audio-controls-time-markers {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.audio-controls-time-markers p {
  margin: 0;
  padding: 0;
}

.audio-slider {
  appearance: none;
  width: 100%;
  height: 10px;
  background: #ddd;
  border-radius: 5px;
  outline: none;
}

.audio-slider::-webkit-slider-thumb {
  appearance: none;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: #fff;
  border-radius: 50%;
}

.audio-slider::-moz-range-thumb {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: #fff;
  border-radius: 50%;
}

.footer-container {
  min-height: 224px;
  background-color: #fafafa;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  padding-top: 48px;
  display: flex;
}

@media only screen and (max-width: 1008px) {
  .container {
    max-width: 85vw;
  }
}

